.card{
    width: 200px;
    margin: 20px;
    height: 300px;
    padding: 15px;
    box-shadow: 0px 0px 50px 0px rgba(0, 0, 0, 0.1);
    border-radius: 25px;
}
.card__image{
    width: 200px;

}
.card__button{
    font-family: 'Archivo Narrow', sans-serif;
    width: 200px;
    height: 50px;
    background-color: #18c1c1;
    color: #fff;
    border: none;
    border-radius: 12px;
    font-size: 20px;
    font-weight: bold;
    cursor: pointer;
}
.card:hover{
    transform: scale(1.05);
    transition: 500ms;
    cursor: pointer;
}

@media screen and (max-width: 991px) {
    .card{
        width: 150px;
        margin: 20px;
        height: 240px;
        padding: 15px;
        box-shadow: 0px 0px 50px 0px rgba(0, 0, 0, 0.1);
        border-radius: 25px;
    }

    .card__image{
        width: 150px;
    }

    .card__button{
        font-family: 'Archivo Narrow', sans-serif;
        width: 150px;
        height: 50px;
        background-color: #18c1c1;
        color: #fff;
        border: none;
        border-radius: 12px;
        font-size: 14px;
        font-weight: bold;
        cursor: pointer;
    }

}

@media screen and (max-width: 768px) {
    .card{
        width: 100vw;
        margin-bottom: -30px;
        height: 50px;
        box-shadow: none;

    }
    .card__image{
    display: none;;    
    }
    .card__button{
        width: 90vw;
        height: 50px;
        font-size: 15px;
        margin-left: 55vw;

    }
    .card:hover{
        transform: scale(1.0);
        transition: 500ms;
        cursor: pointer;
    }
}
    
