
.MainFrame {
    padding-top: 50px;
    width: 100vw;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.AnswerFrame {
    width: 100vw;
    display: flex;
    flex-direction: row;
    justify-content: center;
    gap: 20px;
    
    align-items: center;
}

.Question{
    font-family: 'Archivo', sans-serif;
    font-size: 32px;
    font-weight: bold;
    color: #282cb9;
    margin: 0px;
    padding: 0px;
    margin-bottom: 50px;
}

.Formular{
    width: 100vw;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.Formular input{
    width: 70vw;
    height: 20px;
    border-radius: 0px;
    border:none;
    font-size: 20px;
    font-family: 'Archivo', sans-serif;
    font-weight: normal;
    background-color: rgb(248, 248, 248);
    margin: 10px;
    padding: 10px;
}

.Formular input:focus{
    outline: solid 0.5px #282cb9;
}

.Formular button{
    width: 70vw;
    height: 50px;
    font-size: 15px;
    font-family: 'Archivo Light', sans-serif;
    background-color: #18c1c1;
    color: #fff;
    border: none;
    border-radius: 12px;
    font-weight: bold;
    cursor: pointer;

}

.Formular .Checkbox{
    width: 100vw;
    display: block;
    text-align: left;
}
.Formular .Checkbox :focus{
    outline: none;
}

.labelTxt{
    font-family: 'Archivo', sans-serif;
    font-size: 15px;
    font-weight: normal;
    color: #282cb9;
    margin-top: 50px;
    padding: 0px;
    margin-bottom: 20px;
    text-align: left;
    width: 70vw;
}

@media screen and (max-width: 768px) {
    .MainFrame {
        padding-top: 50px;
        width: 100vw;
    }
    .AnswerFrame{
        display: flex;
        flex-direction: column;    
        justify-content: center;
        width: 100vw;

        transform: translateX(-50%);

    }
    .Question{
        font-size: 24px;
        padding: 20px;
    }
    .Formular{
        margin-left:100%;
    }

    
    
}